import React, { useState } from "react";
import "./App.css";
import Form from "./components/Form";
import Passages from "./components/Passages";
// import PassageCore from "./components/Passage_Core";
function App() {
  const [passages, setPassages] = useState([]);
  const [darken, setDarken] = useState(false);
  const [count, setCount] = useState(0);

  // const [hidden, setHidden] = useState(true);
  return (
    <div className="App">
      <div className={darken ? "darken" : ""}></div>
      <Form
        passages={passages}
        setPassages={setPassages}
        count={count}
        setCount={setCount}
      />
      <Passages
        passages={passages}
        setPassages={setPassages}
        setDarken={setDarken}
        count={count}
      />
    </div>
  );
}

export default App;
